// src/pages/Cafe.js

import axios from "axios";
import { useEffect, useState } from "react";
import { Pagination, Table } from "react-bootstrap";
import { Link, useNavigate, useSearchParams } from "react-router-dom";


function Cafe() {
    //     
    const [pageInfo, setPageInfo]=useState({
        list:[]
    })
    //     
    const [searchState, setSearchState]=useState({
        condition:"",
        keyword:""
    })
    // "cafes/pageNum=x"  pageNum   Hook
    const [params, setParams]=useSearchParams({pageNum:1})

    //         
    const [pageArray, setPageArray]=useState([])

    //       
    const refresh = (pageNum)=>{
        //    query  
        const query=new URLSearchParams(searchState).toString()
        // axios  pageNum   . 
        axios.get(`/cafes?pageNum=${pageNum}&${query}`)
        .then(res=>{
            console.log(res.data)
            //    state .
            setPageInfo(res.data)
            //      state .
            const result=range(res.data.startPageNum, res.data.endPageNum)
            setPageArray(result)
        })
        .catch(error=>{
            console.log(error)
        })
    }

    /*
        useEffect()    
        1. Cafe     1 .
        2. params    .
    */

        useEffect(()=>{
            // query   .
            let pageNum=params.get("pageNum")
            //     1 
            if(pageNum === null)pageNum=1
            //      . 
            refresh(pageNum)
        }, [params])

        //   hook
        const navigate = useNavigate()

        //  UI      
        function range(start, end){
            const result = [];
            for (let i = start; i<=end; i++) {
                result.push(i);
            }
            return result;
        }

        // navigate()     
        const move =(pageNum=1)=>{
            //   query  
            const query = new URLSearchParams(searchState).toString()
            navigate(`/cafes?pageNum=${pageNum}&${query}`)
        }
        //       
        const handleSearchChange = (e)=>{
            setSearchState({
                ...searchState,
                [e.target.name]:e.target.value //   or     
            })
        }

        // Reset    
        const handleReset = ()=>{
            setSearchState({
                condition:"",
                keyword:""
            })
            // 1   
            move(1)
        }

    return (
        <>
            <Link to="/cafes/new"> </Link>
            <h1>Cafe   </h1>
            <Table striped bordered size="sm">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {
                        pageInfo.list.map(item=>(
                            <tr key={item.num}>
                                <td>{item.num}</td>
                                <td>
                                    <Link to={`/cafes/${item.num}?condition=${searchState.condition}&keyword=${searchState.keyword}`}>{item.title}</Link>
                                </td>
                                <td>{item.title}</td>
                                <td>{item.writer}</td>
                                <td>{item.viewCount}</td>
                                <td>{item.regdate}</td>
                            </tr>
                        ))
                    }
                </tbody>
            </Table>
            <Pagination className="mt-3">
                <Pagination.Item onClick={()=>move(pageInfo.startPageNum -1)} disabled={pageInfo.startPageNum === 1}>&laquo;</Pagination.Item>
                {
                    pageArray.map(item=>(
                        <Pagination.Item onClick={()=>move(item)} key={item} active={pageInfo.pageNum === item}>
                            {item}
                        </Pagination.Item>
                    ))
                }
                <Pagination.Item onClick={()=>move(pageInfo.endPageNum+1)} disabled={pageInfo.endPageNum >= pageInfo.totalPageCount}>&raquo;</Pagination.Item>
            </Pagination>
            <label htmlFor="search"></label>
            <select onChange={handleSearchChange} value={searchState.condition} name="condition" id="search">
                <option value=""></option>
                <option value="title_content">+</option>
                <option value="title"></option>
                <option value="writer"></option>
            </select>
            <input onChange={handleSearchChange} value={searchState.keyword} type="text" placeholder="..." name="keyword" />
            <button onClick={()=>move()}></button>
            <button onClick={handleReset}>Reset</button>
            <p><strong>{pageInfo.totalRow}</strong>   </p>
        </>
    );
}

export default Cafe;