import axios from "axios"
import React, { useEffect, useState } from "react"
import { Button, Card, Pagination } from "react-bootstrap"
import { useSelector } from "react-redux"
import { Link, useNavigate, useParams } from "react-router-dom"

function GalleryDetail(props) {
  // "gallery/:num"     
  const { num } = useParams()

  // galley    
  const [state, setState] = useState(null)

  const navigate = useNavigate()
  //    store   .
  const userName = useSelector((state) => state.userName)
  // useEffect()   Component     1 
  // num     .
  useEffect(() => {
    axios
      .get(`/gallery/${num}`)
      .then((res) => {
        console.log(res.data)
        //     state .
        setState(res.data)
      })
      .catch((error) => {
        console.log(error)
      })
  }, [num])

  const handleDelete = () => {
    axios.delete(`/gallery/${num}`).then((res) => {
      console.log(res.data)
      navigate(`/gallery`)
    })
  }
  return (
    <>
      <h1>Gallery   </h1>
      {state && (
        <>
          <Pagination>
            <Pagination.Item as={Link} to={`/gallery/${state.prevNum}`}>
              &larr; Prev
            </Pagination.Item>
            <Pagination.Item as={Link} to={`/gallery/${state.nextNum}`}>
              &rarr; Next
            </Pagination.Item>
          </Pagination>
          <Card>
            <Card.Img variant="top" src={`/upload/images/${state.saveFileName}`} />
            <Card.Body>
              <Card.Text>{state.caption}</Card.Text>
              <Card.Text>writer : {state.writer}</Card.Text>
              <Card.Text>{state.regdate}</Card.Text>
              {userName === state.writer && (
                <Button variant="danger" onClick={handleDelete}>
                  
                </Button>
              )}
            </Card.Body>
          </Card>
        </>
      )}
    </>
  )
}

export default GalleryDetail
