import axios from 'axios';
import React, { useRef, useState } from 'react';
import { Button, Col, Form, Row } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

function GalleryForm(props) {
    
    const dropZone = useRef()
    const imageInput = useRef()
    const captionInput = useRef()
    //        
    const [imageData , setImageData] = useState([])
    const navigate = useNavigate()
    const [state, setState]=useState([])
    //       
    

    const dropZoneStyle={
        minHeight:"300px",
        border:"3px solid #cecece",
        borderRadius:"10px",
        display:"flex",
        justifyContent:"center",
        alignItems:"center",
        cursor:"pointer",
        flexWrap:"wrap",
        rowGap:"10px",
        columnGap:"10px"
    }

    const selectedStyle={
        width:"300px",
        borderRadius:"10px"
    }

    //  drop    
    const handleDrop = (e)=>{
        e.preventDefault()
       
        //drop      
		const files=e.dataTransfer.files;
			
        //    
        const imageFiles=[];
			
        //javascript 
        for(let i=0; i<files.length; i++){
            //i  File 
            const tmp=files[i];
            //         
            const reg=/image/;
            if(!reg.test(tmp.type)){ // type     
                console.log("  ")
                continue; //    
            }
           
            //    
            imageFiles.push(tmp);
            //     
            const reader=new FileReader();
            // DataURL   
            reader.readAsDataURL(tmp);
            // (  )    
            reader.onload=(event)=>{
                //    
                const data=event.target.result;
                const result=imageFiles.map(item=>{
                    //         
                    if(item.name === tmp.name){
                        item.dataUrl = data // dataUrl      .
                    }
                    return item
                })
                // state  
                setImageData(result)
                console.log(imageFiles)
            };
        }
        
        // input     
        //DataTransfer  
        const dataTransfer = new DataTransfer();
        // imageFiles       DataTransfer     
        imageFiles.forEach(item => dataTransfer.items.add(item));
        
        // input type="file"  DataTransfer      
        imageInput.current.files=dataTransfer.files;
    }

    
    const handleChange = (e)=>{
        //  
        const files=e.target.files;
        //    
        const imageFiles=[];

        //javascript 
        for(let i=0; i<files.length; i++){
            //i  File 
            const tmp=files[i];
            //         
            const reg=/image/;
            if(!reg.test(tmp.type)){ // type     
                console.log("  ")
                continue; //    
            }
        //    
        imageFiles.push(tmp);
         //     
         const reader=new FileReader();
         // DataURL   
         reader.readAsDataURL(tmp);
         // (  )    
         reader.onload=(event)=>{
             //    
             const data=event.target.result
                const result=imageFiles.map(item=>{
                    //         
                    if(item.name === tmp.name){
                        item.dataUrl = data // dataUrl      .
                    }
                    return item
                })
             
              // state  
              setImageData(result)
            };
         }
    }

    //     
    const handleUpload = ()=>{
        //    FormData   
        const formData=new FormData()
        // caption     
        formData.append("caption", captionInput.current.value)
        //     images   
        const files=imageInput.current.files //   
        for(let i=0; i<files.length; i++){
            formData.append("images", files[i])
        }
       
        axios.post("/gallery", formData, {
            headers:{"Content-Type":"multipart/form-data"}
        })
        .then(res=>{
            console.log(res.data)
            navigate("/gallery")
        })
    }


    return (
        <>

            <Form.Group as={Row} className="mb-3" controlId="caption">
                <Form.Label column sm={2}> </Form.Label>
                <Col sm={10}>
                    <Form.Control ref={captionInput} type="text" name="caption" placeholder=" " />
                </Col>
            </Form.Group>
           
            <Form.Group as={Row} className="mb-3" controlId="image">
                <Form.Label column sm={2}> </Form.Label>
                <Col sm={10}>
                    <Form.Control ref={imageInput} onChange={handleChange} type="file" accept="image/*" name="image" multiple />
                </Col>
            </Form.Group>
            <a href="about:blank" onClick={(e)=>{
                e.preventDefault()//      
                imageInput.current.click() //     
            }}>
            <div ref={dropZone} onDragOver={(e)=>e.preventDefault()} 
                onDrop={handleDrop} style={dropZoneStyle} className="mb-3">
                {
                    // item File ,  dataUrl    .
                    imageData.length > 0 ? imageData.map(item=><img style={selectedStyle} key={item.name} src={item.dataUrl} alt={item.name}/>)
                    : <strong>Drag and Drop Files <br />or Click Here!</strong>
                }
            </div>
            </a>
            <Button onClick={handleUpload} variant="primary"></Button>
            
        </>
    );
}

export default GalleryForm;