// src/pages/UserPwdUpdateForm.js

import axios from "axios"
import { useState } from "react"
import { Alert, Button, Form } from "react-bootstrap"
import { useDispatch } from "react-redux"
import { useNavigate } from "react-router-dom"

//    1     
const reg_password = /^[^\s]+$/
//    
const reg_newPassword = /[\W]/

function UserPwdUpdateForm() {
  //    
  const [formData, setFormData] = useState({
    password: "",
    newPassword: "",
    newPassword2: "",
  })

  //    
  const [isValid, setValid] = useState({
    password: false,
    newPassword: false,
  })

  //     
  const [isDirty, setDirty] = useState({
    password: false,
    newPassword: false,
  })

  //   
  const [error, setError] = useState({
    show: false,
    message: "",
  })

  // input  change     
  const handleChange = (e) => {
    // e.target   name  value   object   object    .
    const { name, value } = e.target

    //     
    if (!isDirty[name]) {
      //   
      setDirty({
        ...isDirty,
        [name]: true,
      })
    }

    setFormData({
      ...formData,
      [name]: value,
    })
    validate(name, value)
  }

  // 
  const validate = (name, value) => {
    if (name === "password") {
      setValid({
        ...isValid,
        [name]: reg_password.test(value),
      })
    } else if (name === "newPassword") {
      const isEqual = value === formData.newPassword2
      setValid({
        ...isValid,
        newPassword: reg_newPassword.test(value) && isEqual,
      })
    } else if (name === "newPassword2") {
      const isEqual = value === formData.newPassword
      setValid({
        ...isValid,
        newPassword: reg_newPassword.test(value) && isEqual,
      })
    }
  }

  const navigate = useNavigate()
  const dispatch = useDispatch()

  //     
  const handleSubmit = (e) => {
    e.preventDefault()
    axios
      .patch("/user/password", formData)
      .then((res) => {
        console.log(res.data)
        //    ?
        //1.   
        delete localStorage.token
        //2.  /   
        navigate("/")
        //3. "    "     
        alert("     .")
        // 0.1     
        setTimeout(() => {
          //    ProtectedRoute   
          dispatch({ type: "UPDATE_USER", payload: null })
        }, 100)

        const payload = {
          show: true,
          message: "    ",
        }
        //  action   payload  .
        dispatch({ type: "LOGIN_MODAL", payload })
      })
      .catch((error) => {
        console.log(error)
        //     ?
        // 
        const message = error.response.data.message
        setError({
          show: true,
          message,
        })
      })
  }

  return (
    <>
      <h1>  </h1>
      {error.show && <Alert variant="danger">{error.message}</Alert>}
      <Form onSubmit={handleSubmit}>
        <Form.Group className="mb-3">
          <Form.Label>  </Form.Label>
          <Form.Control
            isValid={isValid.password}
            isInvalid={!isValid.password && isDirty.password}
            onChange={handleChange}
            type="password"
            name="password"
          />
          <div className="form-text"> !</div>
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label>  </Form.Label>
          <Form.Control
            isValid={isValid.newPassword}
            isInvalid={!isValid.newPassword && isDirty.newPassword}
            onChange={handleChange}
            type="password"
            name="newPassword"
          />
          <div className="form-text">     </div>
          <Form.Control.Feedback type="invalid"> !</Form.Control.Feedback>
        </Form.Group>
        <Form.Group className="mb-3">
          <Form.Label>   </Form.Label>
          <Form.Control onChange={handleChange} type="password" name="newPassword2" />
        </Form.Group>
        <Button disabled={!isValid.password || !isValid.newPassword} variant="success" size="sm" type="submit">
          
        </Button>
      </Form>
    </>
  )
}

export default UserPwdUpdateForm
