import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';

import reportWebVitals from './reportWebVitals';
import { RouterProvider } from 'react-router-dom';
// legacy_createStore  createStore    (store   )
import { legacy_createStore as createStore } from 'redux';
// src/router/router.js import
import router from './router/router';
import axios from 'axios';
import { decodeToken } from 'jsontokens';
import { Provider } from 'react-redux';


// Token  , Token      
let userName=null
//let isLogin=false

//   
if(localStorage.token){
  //   (  7  , Bearer+    )
  const result = decodeToken(localStorage.token.substring(7))
  console.log("    ")
  console.log(result)
  // expire       1000  ms  .
  const expTime = result.payload.exp*1000
  //  ms  
  const now = new Date().getTime()
  //     
  if(expTime > now){
    //    subject(userName)  .
    userName=result.payload.sub
    //isLogin=true
    //axios  header        
    axios.defaults.headers.common["Authorization"]=localStorage.token
  }else{
    //   
    delete localStorage.token
  }
}

const loginModal={
  show:false,
  message:"",
  url:null
}

// store   state  
const initialState={userName, loginModal}

//reducer  (action    )
const reducer = (state=initialState, action)=>{
  let newState
  if(action.type === "UPDATE_USER"){
    newState={
      ...state,
      userName:action.payload
    }
  //}else if(action.type === "SET_LOGIN"){
    //newState={
    //  ...state,
    //  isLogin:action.payload
    //}
  }else if(action.type === "LOGIN_MODAL"){
    newState={
      ...state,
      loginModal:action.payload
    }
  }else{
    newState=state
  }

  return newState
}
// reducer   store()  
const store=createStore(reducer)

//id  root   UI  
const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  //<React.StrictMode>
     <Provider store={store}>
    <RouterProvider router={router} />
    </Provider>
  //</React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
