import axios from "axios";
import { useEffect, useState } from "react";
import 'bootstrap/dist/css/bootstrap.css';
import { Link, useNavigate } from "react-router-dom";

// axios  get  /members      
function Member() {

    const navigate = useNavigate()

    const reflash = ()=>{
        axios.get("/members")
        .then(res=>SetMembers(res.data))
        .catch(error=>console.log(error))
    }
    const [state, setState]=useState({})
    //    
    const [members, SetMembers]=useState([]);
    // Member componenet       
    // useEffect(, )  component     1 . 
    useEffect(()=>{
        //  component      .
        axios.get("/members")
        .then(res=>{
        console.log(res.data)
        //  state  
        SetMembers(res.data)
      
      })
      .catch(error=>{
        console.log(error)
      })
    }, [])        
    const handleDelete=(num)=>{
        axios.delete("/members/"+num)
        .then(res=>{
        console.log(res.data)
        setState({
            ...state,
        })
        alert(num+"  .")
        //  state  
        reflash()
        })
        .catch(error=>{
        console.log(error);
        });
        
    }
   
    return (
        <>
            <Link to="/members/new"></Link>
            <h1> </h1> 
            <p>  .</p>
            <table className="table table-bordered" size="sm" >
                <thead>
                    <tr className="table-primary">
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    { members.map(item=><tr key={item.num}> 
                        <td>{item.num}</td>
                        <td>{item.name}</td>
                        <td>{item.addr}</td>
                        <td><button className="btn btn-info" onClick={()=>{
                            //    
                            navigate(`/members/${item.num}/edit`)
                        }}></button></td>
                        <td><button className="btn btn-danger" onClick={()=>{handleDelete(item.num)}}></button></td>
                        </tr>)}
                
                </tbody>


            </table>
        </>
    );
}

export default Member;