// App.css  ( css)
import { NavLink, useNavigate, useOutlet } from "react-router-dom"
import "bootstrap/dist/css/bootstrap.css"
import BsNavBar from "./components/BsNavBar"
import LoginModal from "./components/LoginModal"
import { useDispatch, useSelector } from "react-redux"
import { useEffect, useState } from "react"
import { decodeToken } from "jsontokens"
import axios from "axios"
import AlertModal from "./components/AlertModal"

// component
function App() {
  //  route    hook
  const currentOutlet = useOutlet()
  //    redux store   .
  const loginModal = useSelector((state) => state.loginModal) // loginModal 3  (show, message, url)
  // action   
  const dispatch = useDispatch()

  //   
  const [alertShow, setAlertShow] = useState(false)
  // store  userName .
  const userName = useSelector((state) => state.userName)
  // timeout   
  const [timeoutId, setTimeoutId] = useState({
    id1: null,
    id2: null,
  })
  const navigate = useNavigate()

  // App      userName    
  // userName   Id,  null .
  useEffect(() => {
    if (timeoutId.id1) {
      clearTimeout(timeoutId.id1)
      clearTimeout(timeoutId.id2)
      setTimeoutId({
        id1: null,
        id2: null,
      })
    }

    if (localStorage.token) {
      const result = decodeToken(localStorage.token.substring(7))
      //expire       1000   ms   
      const expTime = result.payload.exp * 1000
      //  ms   
      const now = new Date().getTime()
      //    
      if (expTime > now) {
        //   .
        const remainTime = expTime - now
        //    .
        const id1 = setTimeout(() => {
          setAlertShow(true)
        }, remainTime - 1000 * 60 * 5)
        //       
        const id2 = setTimeout(() => {
          // locatStorage token .
          delete localStorage.token
          // axios     
          delete axios.defaults.headers.common["Authorization"]
          alert("  .")
          // 0.1  redux store  .
          setTimeout(() => {
            dispatch({ type: "UPDATE_USER", payload: null })
            navigate("/")
          }, 100)
        }, remainTime)
        // timeout    
        setTimeoutId({ id1, id2 })
      }
    }
  }, [userName])
  return (
    <>
      <AlertModal
        show={alertShow}
        message="5   .   !"
        yes={() => setAlertShow(false)}
      />
      <BsNavBar />
      <div className="container" style={{ marginTop: "60px" }}>
        <div>{currentOutlet}</div>
      </div>
      {loginModal.show && (
        <LoginModal
          onHide={() => dispatch({ type: "LOGIN_MODAL", payload: { show: false } })}
          show={loginModal.show}
          message={loginModal.message}
          url={loginModal.url}
        />
      )}
    </>
  )
}

// App.js  import  App   . (src/index.js)
export default App
