import React, { useState } from "react"
import { Button, Container, Nav, Navbar } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { Link, NavLink, useNavigate } from "react-router-dom"
import AlertModal from "./AlertModal"
import axios from "axios"

function BsNavBar() {
  //     store .
  const userName = useSelector((state) => state.userName)
  const kakaoId = useSelector((state) => state.kakaoId)

  // action dispatch    
  const dispatch = useDispatch()
  const navigate = useNavigate()

  //" "   
  const [alertShow, setAlertShow] = useState(false)

  const handleLogout = () => {
    // localStorage  token 
    delete localStorage.token
    // userName null 
    dispatch({ type: "UPDATE_USER", payload: null })
    //   
    navigate("/")
    //   
    setAlertShow(true)
    // axios  Header    
    delete axios.defaults.headers.common["Authorization"]
  }

  const handleYes = () => {
    //   
    setAlertShow(false)
  }

  const G_REDIRECT_URL = "http://localhost:3000/googleLogin/redirect"
  const G_CLIENT_ID_API_KEY = "813308724720-70o3vscmtc40nt6v9llmj7t96l28k3sp.apps.googleusercontent.com"
  const googleURL =
    "https://accounts.google.com/o/oauth2/v2/auth?client_id=" +
    G_CLIENT_ID_API_KEY +
    "&redirect_uri=" +
    G_REDIRECT_URL +
    "&response_type=code" +
    "&scope=email profile"

  const K_REST_API_KEY = "ea3fc29935a7e7b17c9a328b221b9488"
  const k_REDIRECT_URL = "http://localhost:3000/kakaoLogin/redirect"
  const kakaoURL =
    "https://kauth.kakao.com/oauth/authorize?client_id=" +
    K_REST_API_KEY +
    "&redirect_uri=" +
    k_REDIRECT_URL +
    "&response_type=code"

  const handlegoogleLogin = () => {
    window.location = googleURL
  }

  const handlekakaoLogin = () => {
    window.location = kakaoURL
  }

  const handlekakaoLogout = () => {
    const token = window.localStorage.getItem("KakaoToken")
    const kakaoId = window.localStorage.getItem("kakaoId")
    console.log(token)
    const authHeader = token.substring(7)
    console.log(authHeader)

    if (!token) {
      console.error(" .  .")
      return
    }

    axios
      .post(
        "/api/v1/auth/kakaoLogout",
        { kakaoId },
        {
          headers: {
            Authorization: `Bearer ${authHeader}`,
            "Content-Type": "application/x-www-form-urlencoded",
          },
        }
      )
      .then((res) => {
        console.log(res.data) //    
        window.localStorage.removeItem("KakaoToken") //  
        window.localStorage.removeItem("kakaoId")
        alert(" .")
        //    (  )
      })
      .catch((error) => {
        console.error("  :", error)
        alert(" .")
      })

    const handlegoogleLogin = () => {}
  }

  return (
    <>
      <AlertModal show={alertShow} message=" !" yes={handleYes} />
      <Navbar fixed="top" expand="md" className="bg-primary mb-2" data-bs-theme="dark">
        <Container>
          <Navbar.Brand as={NavLink} to="/">
            Acorn
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="one" />
          <Navbar.Collapse id="one">
            <Nav className="me-auto">
              <Nav.Link as={NavLink} to="/">
                Home
              </Nav.Link>
              <Nav.Link as={NavLink} to="/members">
                Member
              </Nav.Link>
              <Nav.Link as={NavLink} to="/posts">
                Post
              </Nav.Link>
              <Nav.Link as={NavLink} to="/gallery">
                Gallery
              </Nav.Link>
              <Nav.Link as={NavLink} to="/cafes">
                Cafe
              </Nav.Link>
            </Nav>
            {userName ? (
              <>
                <Nav>
                  <Nav.Link as={Link} to="/user/detail">
                    {userName}
                  </Nav.Link>
                  <span className="navbar-text">Signed in</span>
                </Nav>
                <Button size="sm" onClick={handleLogout} variant="outline-danger">
                  Logout
                </Button>
              </>
            ) : (
              <>
                <Button
                  size="sm"
                  variant="success"
                  onClick={() => {
                    //    action dispatch .
                    const action = {
                      type: "LOGIN_MODAL",
                      payload: {
                        show: true,
                        message: "  ",
                      },
                    }
                    dispatch(action)
                  }}>
                  Sign in
                </Button>

                <Button size="sm" variant="success" onClick={() => navigate("/user/new")}>
                  Sign up
                </Button>

                <Button className="ms-1" size="sm" variant="info" onClick={handlekakaoLogin}>
                  kakao 
                </Button>

                <Button className="me-1" size="sm" variant="info" onClick={handlegoogleLogin}>
                  google 
                </Button>
                <Button variant="outline-danger" onClick={handlekakaoLogout}>
                   
                </Button>
              </>
            )}
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </>
  )
}

export default BsNavBar
