
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.css';
import { useState } from 'react';
import { Form, Button, Col, Row } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';


function MemberForm() {
    //    javascript    
    const navigate = useNavigate()

    const [state, setState]=useState({})
    //        
    const handleChange = (e)=>{
        // change event      state  .
        setState({
            ...state,
            [e.target.name]:e.target.value
        })
    }
    const handleSave=()=>{
        // state   object .( body json  )
        // state    json  objcet  .
        axios.post("/members", state)
        .then(res=>{
        console.log(res.data)
        alert(res.data.name+"  .")
        //   "/member/new"  "/members"    .
        navigate("/members")
        //  state  
        setState(res.data)
        })
        .catch(error=>{
        console.log(error);
        });
    }
    return (
        <>
            
            <h1>  </h1><br />
            
            <Form>
            <Form.Group as={Row} className="mb-3" controlId="name">
                <Form.Label column sm={2}> </Form.Label>
                <Col sm={10}>
                    <Form.Control onChange={handleChange} type="text" name="name"placeholder=" " />
                </Col>
            </Form.Group>
            <Form.Group as={Row} className="mb-3" controlId="addr">
                <Form.Label column sm={2}> </Form.Label>
                <Col sm={10}>
                    <Form.Control onChange={handleChange} type="text" name="addr" placeholder=" " />
                </Col>
            </Form.Group>
            <Form.Group as={Row} className="mb-3">
                <Col sm={{ span: 10, offset: 2 }}>
                    <Button onClick={handleSave} variant="primary"></Button>
                </Col>
            </Form.Group>
            </Form>
            
        </>
    );
}

export default MemberForm;